
























































































	var loginRes, _self;
	import uniGrid from "@dcloudio/uni-ui/lib/uni-grid/uni-grid.vue"
	import uniList from '@dcloudio/uni-ui/lib/uni-list/uni-list.vue';
	import uniListItem from '@dcloudio/uni-ui/lib/uni-list-item/uni-list-item.vue';
	export default {
		components: {
			uniGrid,
			uniList,
			uniListItem
		},
		data() {
			return {
				storageData: {},
				articlenum: '0',
				attennum: '0',
				messnum: '0',
				scores: '0',
				swiperItems: [{
						"imgUrl": "../../static/read1.jpg",
						"path": "../index/index",
						"title": "简阅",
						"openType": "navigate"
					},
					{
						"imgUrl": "../../static/read2.jpg",
						"path": "../index/index",
						"title": "简阅",
						"openType": "switchTab"
					},
					{
						"imgUrl": "../../static/read3.jpg",
						"path": "../index/index",
						"title": "简阅",
						"openType": "switchTab"
					},
					{
						"imgUrl": "../../static/read4.jpg",
						"path": "../index/index",
						"title": "简阅",
						"openType": "switchTab"
					}
				],
				follows:[],
			    fans:[],
				articles:[]
			};
		},
		onLoad: function() {
			/* 	_self = this;
				uni.request({
					url: 'https://www.easy-mock.com/mock/5bb833775df5622d84ac87ca/example/swiperwithtitle#!method=get',
					success: function(res) {
						console.log(res);
						_self.swiperItems = res.data.data;
					}
				}); */
		},
		onShow: function() {
			var _this = this;
			const loginKey = uni.getStorageSync('login_key');
			console.log("come");
			if (loginKey) {
				console.log(loginKey);
				this.storageData = {
					login: loginKey.login,
					nickname: loginKey.nickname,
					avatar: loginKey.avatar
				};
			} else {
				this.storageData = {
					login: false
				};
			};
            this.getFollows();
			this.getFans();
			this.getArticle();
		},
		methods: {
			follow: function() {
				if (uni.getStorageSync('login_key').login === true) {
					uni.navigateTo({
						url: 'follow'
					});
				} else {
					uni.showModal({
						title: '提示',
						content: '对不起您还未登录!',
						cancelText: '再等等',
						cancelColor: '#EA6F5A',
						success: function(res) {
							if (res.confirm) {
								uni.navigateTo({
									url: '../signin/signin'
								});
							} else if (res.cancel) {
								console.log('用户点击取消')
							};
						}
					});
				}
			},
			article: function() {
				if (uni.getStorageSync('login_key').login === true) {
					uni.navigateTo({
						url: '../articles/articles'
					});
				} else {
					uni.showModal({
						title: '提示',
						content: '对不起您还未登录!',
						cancelText: '再等等',
						cancelColor: '#EA6F5A',
						success: function(res) {
							if (res.confirm) {
								uni.navigateTo({
									url: '../signin/signin'
								});
							} else if (res.cancel) {
								console.log('用户点击取消')
							};
						}
					});
				}

			},
			setting: function() {
				if (uni.getStorageSync('login_key').login === true) {
					uni.navigateTo({
						url: '../setting/setting'
					});
				} else {
					uni.showModal({
						title: '提示',
						content: '对不起您还未登录!',
						cancelText: '再等等',
						cancelColor: '#EA6F5A',
						success: function(res) {
							if (res.confirm) {
								uni.navigateTo({
									url: '../signin/signin'
								});
							} else if (res.cancel) {
								console.log('用户点击取消')
							};
						}
					});
				}
			
			},
			Signin: function() {
				uni.navigateTo({
					url: '../signin/signin'
				});
			},
			getFollows: function() {
				var _this = this;
				uni.request({
					url: this.apiServer + '/follow/followlist?fromUId=' + uni.getStorageSync('login_key').userId,
					method: 'GET',
					header: {
						'content-type': 'application/json'
					},
					success: res => {
						_this.follows = res.data.data;
					}
				});
			},
			getFans: function() {
				var _this = this;
				uni.request({
					url: this.apiServer + '/follow/fanlist?toUId=' + uni.getStorageSync('login_key').userId,
					method: 'GET',
					header: {
						'content-type': 'application/json'
					},
					success: res => {
						_this.fans = res.data.data;
					}
				});
			},
			getArticle: function() {
				var _this = this;
				uni.request({
					url: this.apiServer + '/article/article_list',
					method: 'GET',
					header: {
						'content-type': 'application/x-www-form-urlencoded'
					},
					data: {
						uId: uni.getStorageSync('login_key').userId
					},
					success: res => {
						_this.articles = res.data.data;
					}
				});
			},
		}
	};
